import { OrganizationScope } from './organization-scope';
import { User } from './user';
import { FlowNodeInfo } from './start-process-payload';
import { ProcessDefinition } from './execution-response';
import { Addendum } from './complete-context';

/**
 * 指定参与者信息：需要指定参与者的活动、待指定用户列表
 */
export class AssigneeInfo {

    /**
     * 工作项ID
     */
    workItemId: string;

    /**
     * 参与者列表
     */
    assigneeIds: string[];

    /**
     * 参与者用户列表
     */
    assigneeUsers: User[];

    /**
     * 是否允许指派
     */
    selectAssignee: boolean;

    /**
     * 是否多选
     */
    multiSelect: boolean;

    /**
     * 是否允许全员指派
     */
    allowGlobalAssign: boolean;

    /**
     * 指派范围
     */
    organizationScope: OrganizationScope[];
}

/**
 * 根据流程指定下一步参与者参数
 */
export class AssignParameter {

    workItemId: string;

    assigneeIds: string[];

    comment: string;

    addendum?: Addendum;

    priority: number;
}


/**
 * 完成工作项返回结果
 */
export class CompleteResponse {

    dataId: string;

    needInteraction: boolean;

    nextAssigneeInfos: AssigneeInfo[];

    procInstId: string;

    nextFlowNodes: FlowNodeInfo[];//提交后并行的情况

    procDefs: ProcessDefinition[];
}

