import { AssigneeInfo } from './complete-response';
import { User } from './user';

/**
 * 提交审批上下文参数
 */
export class StartProcessPayload {

    /**
     * 随机生成的ID 无意义
     */
    id?: string;

    /**
     * 流程定义ID
     */
    processDefinitionId?: string;

    /**
     * 流程定义Key
     */
    processDefinitionKey?: string;

    /**
     * 流程分类ID
     */
    bizDefKey?: string;

    /**
     * 数据模型ID（BE上的节点ID）
     */
    dataModelId?: string;

    /**
     * 单据内码
     */
    dataId?: string;

    /**
     * 流程实例名称，可空
     */
    name?: string;

    /**
     * 流程发起人
     */
    startUserId?: string;

    /**
     * 下一环节
     */
    nextFlowNodes?: FlowNodeInfo[];

    /**
     * 参数
     */
    variables?: any;

    /**
     * 是否带返回值
     */
    withResponse?: boolean;

    /**
     * 紧急程度
     */
    priority?: number;

    /**
     * 其他需要指派的节点
     */
    otherAssignNodes: FlowNodeInfo[];

}

export class FlowNodeInfo {

    activityDefinitionId: string;

    activityDefinitionName: string;

    dependency: string;//前置依赖，执行提交时传给后端

    selectedAssignees: User[];

    assigneeInfo: AssigneeInfo;

    resolved: boolean;//参与者是否解析

    anonymous: boolean;

    anonymousText: string;

}

export class Assignee {

    id: string;

    name: string;

}

export class CancelSubmitPayload {

    procInstId: string;//可以单独根据流程实例ID取消

    dataId: string;//也可以根据dataId+bizDefKey取消

    bizDefKey: string;
}
