/**
 * 提交审批时，记下选择的流程
 */
export class StartProcessCache {

    selectedProcessList: SelectedProcessItem[];

}

/**
 * 提交审批时，记下选择的流程
 */
export class SelectedProcessItem {

    /**
     * 流程分类id
     */
    processCategory: string;

    /**
     * 流程定义id
     */
    processDefinitionId: string;

    /**
     * 流程定义名称
     */
    processDefinitionName: string;

    /**
     * 记录时间
     */
    logTime: Date = new Date();

    constructor(processCategoryId: string, processDefinitionId: string, processDefinitionName: string) {
        this.processCategory = processCategoryId;
        this.processDefinitionId = processDefinitionId;
        this.processDefinitionName = processDefinitionName;
        this.logTime = new Date();

    }

}