import { Component, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { DataColumn, DatagridComponent } from '@farris/ui-datagrid';
import { EditorTypes } from '@farris/ui-datagrid-editors';
import { LoadingService } from '@farris/ui-loading';
import { NotifyService } from '@farris/ui-notify';
import { forkJoin, Observable, of, Subject } from 'rxjs';
import { CommonComment } from '../../entity/comment';
import { CommonCommentFilter } from '../../entity/commonCommentFilter';
import { I18nService } from '../../service/i18n/i18n.service';
import { TaskService } from '../../service/task.service';

@Component({
  selector: 'lib-comment-manage',
  templateUrl: './comment-manage.component.html',
  styleUrls: ['./comment-manage.component.scss']
})
export class CommentManageComponent implements OnInit {

  commentList: CommonComment[] = [];
  originCommentList: CommonComment[] = [];
  commentColumns: DataColumn[] = [];
  confirmSubject = new Subject();//点击确定
  cancelSubject = new Subject();//点击取消

  @Input() filter: CommonCommentFilter;//意见类型

  @ViewChild('dataGrid') dataGrid: DatagridComponent;
  @ViewChild('commentTypeCell') commentTypeCell: TemplateRef<any>;//常用意见类别模板
  @ViewChild('commentOperateCell') commentOperateCell: TemplateRef<any>;//常用意见删除
  @ViewChild('formButtonRef') buttons: TemplateRef<any>;//取消确认按钮

  constructor(
    private i18nService: I18nService,
    private taskService: TaskService,
    private notify: NotifyService,
    private loadingService: LoadingService,
  ) {
  }

  ngOnInit() {
    this.commentColumns = [{
      field: 'message',
      width: 100,
      title: this.i18nService.getValue('comment.static.content'),
      editor: { type: EditorTypes.TEXTBOX, options: {} },
    }, {
      field: 'userId',
      width: 20,
      title: this.i18nService.getValue('comment.static.type'),
      template: this.commentTypeCell
    }, {
      field: 'operate',
      width: 20,
      title: this.i18nService.getValue('comment.static.operate'),
      template: this.commentOperateCell
    }];
    //加载意见列表
    this.loadComments();
  }

  /**
 * 点击常用意见，获取用户常用意见列表
 */
  loadComments() {
    const load = this.loadingService.show();
    this.taskService.getCommentList(this.filter).subscribe((data: any[]) => {
      load.close();
      this.commentList = data;
      this.originCommentList = JSON.parse(JSON.stringify(data));
    }, () => {
      load.close();
    });
  }

  /**
   * 新增一条常用意见
   */
  addCommonComment() {
    const newEntity = CommonComment.newEntity();
    newEntity.sortOrder = this.commentList.length + 1;
    if (this.filter && this.filter.type) {
      newEntity.type = this.filter.type;
    }
    this.commentList = this.commentList.concat(newEntity);
    //新增后直接编辑单元格
    setTimeout(() => {
      this.dataGrid.editCell(newEntity.id, 'message');
    }, 100);
  }

  /**
   * 删除一条意见 data.rowData
   */
  deleteComment(data: any) {
    if (data.rowData.userId !== '*') {
      this.commentList = this.commentList.filter(c => c.id != data.rowData.id);
      this.originCommentList = this.originCommentList.filter(c => c.id != data.rowData.id);
      this.dataGrid.loadData(this.commentList);
      this.taskService.deleteComment(data.rowData.id).subscribe(() => { });
    }
  }

  /**
   * 常用意见点击保存
   */
  saveCommonComment() {
    this.dataGrid.endCellEdit();
    this.commentList = this.commentList.filter(c => c.message);
    this.commentList.forEach(comment => {
      if (this.taskService.getBytesLength(comment.message) > 255) {
        this.notify.warning(this.i18nService.getValue('cueWords.commonCommentTooLong'));
        this.dataGrid.editCell(comment.id, 'message');
        throw new Error("");
      }
    })
    this.commentList.forEach(comment => {
      let origin = this.originCommentList.find(c => c.id == comment.id);
      if (!origin || comment.message !== origin.message || comment.sortOrder != origin.sortOrder) {
        this.taskService.addComment(comment).subscribe(() => { });
      }
    })
    //此处已返回给farris
    this.confirmSubject.next({ action: 'submit', data: this.commentList });
  }

  /**
   * 点击取消（此处已返回给farris）
   */
  clickCancel() {
    this.commentList = this.commentList.filter(c => c.message);
    this.cancelSubject.next({ action: 'cancel', data: this.originCommentList });
  }

  /**
   * 系统预置的不允许编辑删除
   * @param data data.rowData为实体数据
   * @returns
   */
  beforeEdit = (data: any) => {
    if (data.rowData.userId == '*') {
      return of(false);
    }
    return of(true);
  }

  getConfirmSUbject(): Observable<any> {
    return this.confirmSubject.asObservable();
  }

  getCancelSubject(): Observable<any> {
    return this.cancelSubject.asObservable();
  }

}
