import { BasePayload } from "./base-payload";
import { OrganizationScope } from "./organization-scope";

/**
 * 加签模拟接口返回
 */
export class AddSignSimulateResponse {
    /**
     * 组织
     */
    organization: OrganizationScope;
    /**
     * 多人会签方式
     */
    multiInstanceType: MultiInstanceType;
    /**
     * 是否允许再加签
     */
    showSignToSign: boolean;
}

/**
 *  多人会签方式
 */
export enum MultiInstanceType {
    /**
     * 抢占
     */
    Preempt = 'Preempt',
    /**
     * 并行
     */
    Parallel = 'Parallel',
    /**
     * 串行
     */
    Sequential = 'Sequential'

}

/**
 * 加签提交的payload
 */
export class AddSignPayload extends BasePayload {

    /**
     * 加签方式：前加签、后加签、并加签（当前会签）、征询/协办
     */
    actionCode: string;

    /**
     * 多人会签方式：抢占 Preempt、并行会签 Parallel、串行会签Sequential
     */
    multiInstanceType: string;

    /**
     * 会签策略：全员通过allPass、一人通过onePass
     */
    counterSignRule: string;

    /**
     * 办理规则
     * all所有用户都办理
     * compliance符合规则往下流转
     */
    completedRule: string;

    /**
     * 加签人员
     */
    signAssignees: string[];

    /**
     * 加签节点名称
     */
    activityName: string;

    /**
     * 允许再加签
     */
    allowSignToSign: boolean;

    /**
     * 紧急程度
     */
    priority: number;


}