/**
 * 业务动作参数
 */
export class ActionParameter {
    /**
     * 参数编号
     */
    code: string;
    /**
     * 参数值
     */
    value: string;
}
/**
 * 任务动作定义
 */
export class TaskActionDef {
    /**
     * id
     */
    id: string;
    /**
     * 动作编号
     */
    code: string;
    /**
     * 动作显示名称
     */
    name: string;
    /**
     * 动作实现
     */
    impl: string;
    /**
     * 是否批量操作
     */
    isBatchAction: boolean;
    /**
     * 是否是联查操作
     */
    isHyperlinkAction: boolean;
    /**
     * 是否主操作
     */
    isPrimaryAction: boolean;
    /**
     * 动作排序号
     */
    sortOrder: number;
    /**
     * 类型id
     */
    typeId: string;
    /**
     * 前事件
     */
    preEvents?: ActionEvent[];
    /**
     * 后事件
     */
    postEvents?: ActionEvent[];
}
/**
 * 任务动作定义
 */
export class TaskAction extends TaskActionDef {
    /**
     * 任务动作定义编号
     */
    code: string;
    /**
     * 参数列表
     */
    parameters: ActionParameter[];
}

export class ActionEvent {
    /**
     * ID
     */
    id: string;
    /**
     * 任务分类
     */
    taskCategoryId: string;
    /**
     * 动作编号
     */
    actionCode: string;
    /**
     * 实现
     */
    eventImpl: string;
    /**
     * 排序
     */
    sortOrder: number;
    /**
     * 终端，web，app
     */
    terminal: string;
    /**
     * 类型，Pre,Post
     */
    type: string;
    /**
     * 是否为批量扩展
     */
    batch: boolean;
}
