import { Addendum } from "./complete-context";

/**
 * 执行办理的基础payload
 */
export class BasePayload {

    id?: string;
    /**
     工作项Id
     */
    workItemId: string;
    /**
     备注留言
     */
    comment: string;
    /**
     * 附录信息（含签名、附件）
     */
    addendum: Addendum;
    /**
     审批结论值
     */
    conclusionValue: string;

}