import { Component, OnInit, Input, Output, EventEmitter, ChangeDetectorRef, Injector } from '@angular/core';
import { AssigneeInfo } from '../../entity/complete-response';
import { I18nService } from '../../service/i18n/i18n.service';
import { NotifyService } from '@farris/ui-notify';
import { User } from '../../entity/user';
import { Assignee } from '../../entity/start-process-payload';
import { TaskService } from '../../service/task.service';
import { WfContext } from '../../entity/wfContext';
import { UserHelpParam } from '../../entity/farris-user-help-param';

declare let System: any;

@Component({
  selector: 'lib-assignee-info',
  templateUrl: './assignee-info.component.html',
  styleUrls: ['../task-execute/task-execute.component.css'],
})
export class AssigneeInfoComponent implements OnInit {

  assignUserNgModule: string;
  _assigneeInfo: AssigneeInfo;
  _selectedAssignees: User[];//已经选中的指派人
  assignees: Assignee[] = [];//返回选中人的ID数组
  anonymous = false; // 是否匿名
  anonymousText = ''; // 匿名显示文本
  defaultParticipant: string;//默认办理人（界面提示用）
  pscChange = null;//farris用户帮助

  @Input() disabled: boolean = false;//办理人员输入框是否禁用
  @Input() placeholder: string;
  @Input() singleSelect: boolean;
  @Input() filter = { notGetCurrentUser: false, notGetStopUser: true, orderField: "order", withAllOrg: true, orgId: '' }
  @Input() ifShowBlank: boolean = true;//办理人为空时，是否显示选人框
  @Input() openFromIframe: boolean = false;//是否从iframe打开，应用IDP提交审批集成
  @Input() activityName: string;//节点名称
  @Input() allowGlobalAssign: boolean;//是否全员指派
  @Input() wfContext: WfContext;//工作流上下文
  @Input() index: any;//区分是哪个组件
  @Input() isUseNewSelectUserHelp: boolean = false;//是否直接启用新版选人组件
  @Input() ifHorizontal: boolean = false;//是否水平排列

  @Output() afterConfirm = new EventEmitter<Assignee[]>();
  groupIcon = '<i class="f-icon f-icon-plus-circle text-primary"></i>';
  extendUserHelp: boolean = false;//用户帮助是否扩展
  extendUserHelpUrl: string;//扩展帮助的url

  constructor(
    private i18nService: I18nService,
    private notify: NotifyService,
    private cd: ChangeDetectorRef,
    private taskService: TaskService,
    private injector: Injector
  ) {
    //是否使用farris用户帮助
    this.taskService.getWfUserHelpConfig().subscribe((re: any) => {
      if (re && re.userHelpConfig && re.userHelpConfig.isExtend == 'true') {
        this.extendUserHelp = true;
        this.extendUserHelpUrl = re.userHelpConfig.url + '?v=20221114';
      }
    }, error => {
      console.error(error);
    });
  }

  ngOnInit() {
    //引入farris用户帮助脚本
    if (this.openFromIframe) {
      (top as any).System.import("/platform/common/web/@farris/common-personnel-select/psc.js" + '?v=' + new Date().getTime().toString()).then((e) => {
      }, error => {
        console.error(error);
      });
    }
  }

  @Input()
  set assigneeInfo(info: AssigneeInfo) {
    this.disabled = false;
    this.placeholder = null;//清空
    this.assignUserNgModule = null;//清空
    this._assigneeInfo = info;//可以传null
    if (!this._assigneeInfo) {
      this.disabled = true;
    } else {
      if (this._assigneeInfo.selectAssignee) {
        this.disabled = false;
        this.placeholder = this.i18nService.getValue('task.selectPlaceholder');//请选择
      } else {
        this.disabled = true;
        if (this._assigneeInfo.assigneeUsers && this._assigneeInfo.assigneeUsers.length) {
          this.defaultParticipant = this.anonymous ? this.anonymousText : this._assigneeInfo.assigneeUsers.map(user => user.name).join();//解析人名
        } else {
          this.defaultParticipant = this.i18nService.getValue('task.noUser');//无
        }
      }
      this.singleSelect = !this._assigneeInfo.multiSelect;
      this.allowGlobalAssign = this._assigneeInfo.allowGlobalAssign;
      if (this._assigneeInfo.organizationScope && this._assigneeInfo.organizationScope.length) {
        const orgList = this._assigneeInfo.organizationScope[0];
        this.filter.orgId = orgList.id;
        this.filter.withAllOrg = orgList.includeChildren;
      }
    }
  }

  @Input()
  set setAnonymous(anonymous: boolean) {
    this.anonymous = anonymous;
  }

  @Input()
  set setAnonymousText(anonymousText: string) {
    this.anonymousText = anonymousText;
  }

  /**
   * 已选中的人（帮助不可用）
   */
  @Input()
  set selectedAssignees(selectedAssignees: User[]) {
    !this._assigneeInfo && (this.placeholder = null);
    this._selectedAssignees = selectedAssignees;
    selectedAssignees.length && (this.defaultParticipant = this.anonymous ? this.anonymousText : selectedAssignees.map(user => user.name).join());
  }

  /**
   * 选择参与者
   */
  clickAssignUser() {
    if (this.isUseNewSelectUserHelp) {
      this.extendUserHelp = true;
      this.extendUserHelpUrl = '/platform/runtime/wf/web/wf-user-help/wf-user-help-farris/wf-user-help-farris.js' + '?v=' + new Date().getTime().toString();
    }
    let context = {
      singleSelect: this.singleSelect,
      allowGlobalAssign: this.allowGlobalAssign,
      assigneeUsers: this._assigneeInfo && this._assigneeInfo.assigneeUsers && this._assigneeInfo.assigneeUsers.length ? this._assigneeInfo.assigneeUsers : [],
      filter: this.filter,
      selectedUsers: this.assignees && this.assignees.length ? this.assignees : this._selectedAssignees,
      wfContext: this.wfContext
    }
    if (this.extendUserHelp) {
      if (this.openFromIframe) {
        this.showFarrisUserHelpFromWindow(context);
      } else {
        this.showExtendUserHelp(context);
      }
    } else {
      this.showExtendUserHelp(context);
    }
  }


  /**
   * 加载扩展帮助
   */
  private showExtendUserHelp(context: any) {
    System.import(this.extendUserHelpUrl).then((moduleFactories: any) => {
      const moduleFactory: any = Object.values(moduleFactories)[0];
      const moduleRef = moduleFactory.create(this.injector);
      moduleRef.instance.showHelp(context).subscribe(re => {
        let userList = re.userList;
        if (userList && userList.length) {
          this.assignees = userList.map(user => { return { id: user.id, name: user.name } });
          this.assignUserNgModule = this.assignees.map(user => user.name).join();
        } else {
          this.assignees = [];
          this.assignUserNgModule = '';
        }
        this.afterConfirm.emit(this.assignees);
        this.cd.detectChanges();
      }, error => {
        console.error(error);
      });
    }, error => {
      console.error(error);
    });
  }

  //从window上弹出用户帮助
  private showFarrisUserHelpFromWindow(context: any) {
    let param = new UserHelpParam();
    if (context.wfContext) {
      param.filterId = context.wfContext.processCategoryId;
    }
    param.multiSelect = !context.singleSelect;
    let showOrgTab = false;
    if (context.allowGlobalAssign || (context.filter && context.filter.orgId)) {
      showOrgTab = true;
    }
    let farrisUnSelectedUserIds = '';
    if (context.assigneeUsers && context.assigneeUsers.length) {
      farrisUnSelectedUserIds = context.assigneeUsers.map(user => user.id).join();
      param.unSelectedUserIds = farrisUnSelectedUserIds;
    }
    //有参与者 + 允许全员
    if (showOrgTab && farrisUnSelectedUserIds) {
      param.showTabIds = 'candidate,recent,organization,favorites,group';
      param.activeTabId = 'candidate';
    }
    //允许全员
    if (showOrgTab && !farrisUnSelectedUserIds) {
      param.showTabIds = 'recent,organization,favorites,group';
      param.activeTabId = 'recent';
    }
    //仅有参与者
    if (!showOrgTab && farrisUnSelectedUserIds) {
      param.showTabIds = 'candidate';
      param.activeTabId = 'candidate';
    }
    if (context.filter) {
      let filter = context.filter;
      param.includeCurrentUser = !filter.notGetCurrentUser;
      param.orgId = filter.orgId ? filter.orgId : '*';
      param.enableChildHierarchy = filter.withAllOrg;
    }
    if (context.selectedUsers && context.selectedUsers.length) {
      param.userIds = context.selectedUsers.map(user => user.id).join();
    }
    const token = 'gspWfTask' + this.index;
    (top.window as any).CommonPersonnelSelect.component.farrisDialogShowInWf(param, token);
    if (!this.pscChange) {
      this.pscChange = (top.window as any).FARRIS_PSC.selectionsChange.subscribe((re) => {
        if (re.token == token) {
          let userList = re.data;
          if (userList && userList.length) {
            this.assignees = userList.map(user => { return { id: user.userId, name: user.name } });
            this.assignUserNgModule = this.assignees.map(user => user.name).join();
          } else {
            this.assignees = [];
            this.assignUserNgModule = '';
          }
          this.afterConfirm.emit(this.assignees);
          this.cd.detectChanges();
        }
      }
      );
    }
  }

  //清除
  assignUserClear() {
    this.assignees = [];
    this.assignUserNgModule = null;
    this.afterConfirm.emit([]);
  }

}
