/**
 * 跨iframe通信，消息类型枚举类
 */
export enum MessageType {

    SubmitSuccess = 'SubmitSuccess',//提交成功，要求iframe外弹出提示

    SubmitFailed = 'SubmitFailed',//提交失败，失败信息

    InfoSelectProcess = 'InfoSelectProcess',//提示选择流程

    InfoSelectNode = 'InfoSelectNode',//提示选择节点

    InfoSelectParticipant = 'InfoSelectParticipant',//提示选择办理人

    Cancel = 'Cancel',//点击取消，iframe外关闭弹窗
}

/**
 * 跨iframe通信，消息类
 */
export class PostMessage {

    type: MessageType;

    message: any;
}