
/**
 * 执行上传附件实体
 */
export class FileUploadEntity {

    rootId: string;

    metadataIdList: string[];
}

/**
 * 执行删除附件所需实体
 */
export class FileDeleteEntity {

    rootId: String;

    metadataId: String;

    mode: String;
}

export class GspDocUploadEntity {

    filePath: string;

    metadata: GspDocMetadata;

    fileContent: string;

    mode: string;
}

export class GspDocMetadata {

    id: string;

    fileName: string;

    rootId: string;

    docType: string;
}