import { PriorityItem } from './simulate-response';
import { TaskAction, ActionParameter } from './task-action';

/**
 * 任务状态
 */
export enum TaskState {
    /**
     * 待办
     */
    todo = 2,
    /**
     * 已办
     */
    done = 5,
}
/**
 * 任务实体定义
 */
export class TaskEntity {

    id: string;

    name: string;

    categoryId: string;

    state: TaskState;

    startTime: Date;

    endTime?: Date;

    dueDate?: string;

    targetDate?: string;

    owner: string;

    executor: string;

    sourceId: string;

    description: string;

    taskType: number;

    priority: number;

    priorityName: string;

    priorityItemList?: PriorityItem[];

    submitter: string;

    processInstanceId: string;

    fStr1: string;
    fStr2: string;
    fStr3: string;
    fStr4: string;
    fStr5: string;
    fStr6: string;
    fStr7: string;
    fStr8: string;

    fTime1?: Date;
    fTime2?: Date;
    fTime3?: Date;
    fTime4?: Date;

    fDec1: number;
    fDec2: number;
    fDec3: number;
    fDec4: number;

    fText1: string;
    fText2: string;
    fText3: string;
    fText4: string;

    actions: TaskAction[];
    refBizId: string;
    conclusionValue: string;
}
