import {User} from "./user";

/**
 * 催办模拟接口请求body
 */
export class UrgeHandleSimulateContext {

  procInstId: string;

}

/**
 * 催办模拟返回实体
 */
export class UrgeHandleResponse {

  urgeUsers: UrgeUser[];

}

/**
 * 催办用户返回实体
 */
export class UrgeUser extends User {

  urgeInfos: UrgeInfo[];

  anonymousText: string;

  sendTypes: string;

}

/**
 * 执行催办请求body
 */
export class UrgeHandleContext {

  procInstId: string;

  urgeUsers: UrgeUser[];

  urgeMessageSubject: string;

  urgeMessageContent: string;

  addReminder: boolean;

}

export class UrgeInfo {

  workItemId: string;

  activityInstanceId: string;

  sendTypes: string;

  urgeMessageSubject: string;

  urgeMessageContent: string;

  addReminder: boolean;

  subjectEditable: boolean;

  contentEditable: boolean;
}
