import { Component, OnInit, Input, ViewChild, forwardRef, Output, EventEmitter } from '@angular/core';
import { DialogComponent } from '@farris/ui-dialog';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { FDropdownDirective } from '@farris/ui-dropdown';
import { UploadDialogService } from '@gsp-svc/formdoc-upload';
import { MessagerService } from '@farris/ui-messager';
import { FileDeleteEntity, FileUploadEntity, GspDocMetadata, GspDocUploadEntity } from '../../entity/attachment';
import { SignHereComponent } from '@gsp-wf/wf-sign';
import { AvatarComponent } from '@farris/ui-avatar';
import { DownloadService } from '@gsp-svc/file-load';
import { TaskService } from '../../service/task.service';
import { CommonComment } from '../../entity/comment';
import { LoadingService } from '@farris/ui-loading';
import { ConfigCode, TaskCenterConfig } from '../../entity/task-center-config';
import { CommentManageComponent } from '../comment-manage/comment-manage.component';

@Component({
  selector: 'wf-comment',
  templateUrl: './task-comment.component.html',
  styleUrls: ['./task-comment.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => TaskCommentComponent),
    multi: true
  },]
})
export class TaskCommentComponent implements OnInit, ControlValueAccessor {

  @Input() showAttachment: boolean = true;//是否显示附件上传，组件控制
  @Input() showSign: boolean = true;//是否显示签名，组件控制（不同动作可能会不显示）
  @Input() showCommonComment = true;//是否显示常用意见
  @Input() comment: string;
  @Input() placeholder = '';
  @Input() processInstanceId: string;
  @Input() textAreaHeight: number = 120;//文字框的高度
  @Input() defaultComment: string;//默认的意见
  @Input() curUserId: string;//当前用户id
  @Input() taskOrigin: string;//当前是任务列表打开还是任务办理界面打开

  @Input()
  set TaskCenterConfig(configList: TaskCenterConfig[]) {
    if (configList && configList.length) {
      const attachment = configList.find(a => a.configCode == ConfigCode.If_Show_Attachment);
      const sign = configList.find(a => a.configCode == ConfigCode.If_Show_Signature);
      const enableSign = configList.find(a => a.configCode == ConfigCode.If_Use_Sign_By_Default);
      const editSign = configList.find(a => a.configCode === ConfigCode.If_Can_Edit_Signature);
      attachment && attachment.currentValue === 'true' && (this.ifShowAttachment = true);
      sign && sign.currentValue === 'true' && (this.ifShowSign = true);
      enableSign && enableSign.currentValue === 'false' && (this.ifEnableSign = false);
      editSign && editSign.currentValue === 'false' && (this.ifCanEditSign = false);
      if (this.ifShowSign && this.ifEnableSign) {
        this.getUserSign(this.curUserId);
      }
    }
  }

  @Output() attachment = new EventEmitter();//附件
  @Output() signature = new EventEmitter();//上传签名之后，发送签名id
  @ViewChild('commentDialog') commentDialog: DialogComponent;//常用意见管理
  @ViewChild('signDialog') signDialog: DialogComponent;//手写签名弹框
  @ViewChild('dropdownEl') dropDownEl: FDropdownDirective;//解决选择意见后，下拉菜单无法收折的问题
  @ViewChild('wfSignHere') signaturePad: SignHereComponent;//手写签名组件
  @ViewChild('farrisAvatar') farrisAvatar: AvatarComponent;//头像组件，用作签名图片上传
  @ViewChild('commentManageComp') commentManageComp: CommentManageComponent;//意见维护组件


  private _value = '';
  attachmentList: any[] = [];
  ifShowAttachment: boolean = false;//任务中心配置，是否显示附件上传
  ifShowSign: boolean = false;//任务中心配置，是否显示手写签名
  signatureImage: string;//签名图片的base64值
  tmpSignatureImage: string;//临时保存上传的图片
  downloadService = new DownloadService();
  ifUseSign: boolean = false;//是否使用手写
  ifUseImg: boolean = true;//是否使用上传图片
  ifEnableSign: boolean = true;//是否默认启用签名
  ifUploadImg: boolean = false;//是否上传过图片
  ifCanEditSign: boolean = true;//是否允许编辑签名
  originSignId: string;//原始的签名id
  isShowCommonTips: boolean = false;//是否展示提示
  commentList: CommonComment[] = [];


  btnHover = {//按钮hover样式变化
    commentHover: false,
    attachmentHover: false,
    signHover: false
  }

  set value(v) {
    if (this._value !== v) {
      this._value = v;
      this.onModelChange(v);
    }

  }
  get value() {
    return this._value;
  }

  onModelChange = (obj: any) => { };
  onModelTouched = (obj: any) => { };

  constructor(
    private taskService: TaskService,
    private uploadDialog: UploadDialogService,
    private messageService: MessagerService,
    private loadingService: LoadingService,
  ) {

  }

  ngOnInit() {
    this.commentManageComp.getConfirmSUbject().subscribe(re => {
      this.commentDialog && this.commentDialog.close();
    })
    this.commentManageComp.getCancelSubject().subscribe(() => {
      this.commentDialog && this.commentDialog.close();
    })
  }

  /**
   * 弹出常用意见管理框
   */
  showCommentDialog() {
    this.dropDownEl && this.dropDownEl.close();
    this.commentDialog.show();
  }

  /**
   * 点击常用意见，获取用户常用意见列表
   */
  loadComments() {
    this.taskService.getCommentList().subscribe((data: any[]) => {
      this.commentList = data;
    });
  }

  /**
   * ControlValueAccessor必须实现的方法
   */
  writeValue(value: string) {
    if (value !== this.value) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.onModelChange = fn;
  }

  registerOnTouched(fn) {
    this.onModelTouched = fn;
  }

  /**
   * 选择一条常用意见后事件
   */
  buttonClick(data: any) {
    this.dropDownEl.close();
    this.value = this.value == this.defaultComment ? data : this.value + data;
  }

  /**
   * 上传附件
   */
  uploadAttachment() {
    let isRepeat = false;
    this.uploadDialog.uploadFile(this.processInstanceId ? this.processInstanceId : 'gspwftaskattachment', 'wf_attachment').then(re => {
      for (let i of (re as any[])) {
        isRepeat = false;
        for (let attach of this.attachmentList) {
          if (attach.fileName === i.fileName) {
            this.messageService.info('包含重复文件' + i.fileName);
            isRepeat = true;
            break;
          }
        }
        if (!isRepeat) {
          this.attachmentList.push(i);
        }
      }
      this.attachment.emit(this.attachmentList);
      //执行上传附件
      let files = new FileUploadEntity();
      files.rootId = "wf_attachment";
      files.metadataIdList = [];
      this.attachmentList.forEach(a => {
        files.metadataIdList.push(a.metadataId);
      })
      files.metadataIdList.length && this.taskService.uploadAttachment(files).subscribe(() => { });
    });
  }

  /**
   * 删除附件
   */
  attachmentDelete(attachment) {
    var gspDocDeleteEntity: FileDeleteEntity = {
      rootId: "wf_attachment",
      metadataId: attachment.metadataId,
      mode: "Formal"
    }
    let index = this.attachmentList.indexOf(attachment);
    if (index > -1) {
      this.attachmentList.splice(index, 1);
    }
    this.attachment.emit(this.attachmentList);
    this.taskService.deleteAttachment(gspDocDeleteEntity).subscribe(() => { });
  }

  /**
  * web端打开附件预览
  */
  attachmentPreview(metadataId) {
    const options = {
      appType: "app",
      funcId: "",
      tabName: "文档在线预览",
      appId: "6ccb476b-f2ba-df8f-6f47-d37d100c56e2",
      appEntrance: "docviewertab",
      tabId: new Date().getTime(),
      entityParams: {
        metadataIdList: [metadataId],
        rootId: "wf_attachment",
        metadataId: null,
        viewOptions: {
          showHeader: true,
          showPaging: true,
          showThumbnails: true,
          showDownload: true,
          showSearch: true,
          showZoom: true,
          showPrint: true,
          showSecondaryToolBar: true,
        },
      },
    };
    (window as any).gspframeworkService.rtf.func.openMenu(options);
  }

  /**
   * 获取不同格式附件的缩略图
   */
  imageUrl(attachmentInfo) {
    const url = '/platform/runtime/wf/webapp/mobiletaskcenter/assets/accessory/';
    return url + this.taskService.createItem(attachmentInfo) + '.svg?v=20220303';
  }

  /**
   * 展示手写签名dialog
   */
  showSignDialog() {
    this.signDialog.show();
    if (!this.ifEnableSign) {
      this.getUserSign(this.curUserId);
    }
  }

  /**
   * 点击删除签名
   */
  clickToDeleteImage() {
    this.signatureImage = '';
    this.signature.emit(null);
    const load = this.loadingService.show();
    this.taskService.addUserConfig(ConfigCode.If_Use_Sign_By_Default, 'false').subscribe(re => {
      load.close();
    }, () => {
      load.close();
    })
  }

  clickToUseSign() {
    this.ifUseImg = false;
    this.ifUseSign = true;
    const canvas: any = this.signaturePad.canvasDiv.nativeElement.querySelector('canvas');
    canvas.height = 252;
    canvas.width = 665;
  }

  clickToUploadImg() {
    this.farrisAvatar.onClick();
  }

  /**
   * 手写签名点确定
   */
  clickToConfirm() {
    let image: any;
    if (this.signaturePad && this.signaturePad.isDraw) {//如果签过名
      image = this.signaturePad.confirm();
    } else if (this.ifUploadImg) {//如果上传过图片
      image = this.tmpSignatureImage;
    }
    if (image) {
      this.uploadSignatureImg(image);
    } else {
      this.signatureImage = this.tmpSignatureImage;
      this.signature.emit(this.originSignId);
      this.signDialog.close();
    }
    //添加启用签名配置
    const load = this.loadingService.show();
    this.taskService.addUserConfig(ConfigCode.If_Use_Sign_By_Default, 'true').subscribe(re => {
      load.close();
    }, () => {
      load.close();
    })
  }

  /**
   * 点击清空
   */
  clickToReset() {
    this.ifUseSign = false;
    this.ifUseImg = true;
    this.signatureImage = '';
    this.tmpSignatureImage = '';
    this.signature.emit('');
    this.signaturePad && (this.signaturePad.isDraw = false);
    this.ifUploadImg = false;
  }

  /**
   * 点击取消
   */
  clickToCancel() {
    this.signDialog.close();
  }

  /**
   * 上传图片后事件
   * event.base64 是图片信息,event.name是带后缀的图片名，event.type如image/jpeg
   */
  imgChange(event: any) {
    this.ifUseImg = true;
    this.ifUseSign = false;
    this.ifUploadImg = true;
    const image = event.base64;
    this.tmpSignatureImage = image;
    this.signatureImage = image;
  }

  /**
   * 上传签名
   * @param image 
   */
  private uploadSignatureImg(image: any) {
    var uploadSignature = new GspDocUploadEntity();
    uploadSignature.fileContent = image.split(',')[1];
    uploadSignature.filePath = this.curUserId;
    uploadSignature.mode = "Formal";
    uploadSignature.metadata = new GspDocMetadata();
    uploadSignature.metadata.id = this.taskService.createNM();
    uploadSignature.metadata.fileName = this.taskService.createNM() + ".png";
    uploadSignature.metadata.rootId = "taskcentersignature";
    uploadSignature.metadata.docType = "png";
    const load = this.loadingService.show();
    this.taskService.uploadSignature(uploadSignature).subscribe(signId => {
      load.close();
      this.originSignId = signId;
      this.signature.emit(signId);
      this.signatureImage = image;
      this.signDialog.close();
      localStorage.setItem('gspWfTaskUserSign' + this.curUserId + this.taskService.getCurrentDateString(), signId);
    }, error => {
      load.close();
      this.messageService.error('上传签名失败');
    });
  }

  /**
   * 获取用户的最近一次签名
   */
  private getUserSign(userId: string) {
    const load = this.loadingService.show();
    this.taskService.getUserSignId(userId).subscribe(signId => {
      load.close();
      if (signId) {
        this.signature.emit(signId);
        this.originSignId = signId;
        this.signatureImage = this.downloadService.getDownloadUrl(signId, 'taskcentersignature');
        this.tmpSignatureImage = this.signatureImage;
      }
    }, error => {
      console.error(error);
      load.close();
    })
  }

  //文本框值变动监听事件
  valueChange(e) {
    if (this.taskService.getBytesLength(this.value) > 4000) {
      this.isShowCommonTips = true;
    } else {
      this.isShowCommonTips = false;
    };
  }


}
